<template>
  <div style="margin: 30px">
    <el-divider></el-divider>
    <el-descriptions
      class="margin-top"
      title="模型实验信息"
      style="margin-top: 25px"
      :column="3"
      :size="size">
      <template slot="extra">
        <router-link
          :to="{ name: '模型实验', params: {experiment_id: 
               this.$route.params.experiment_id}}"
          style="text-decoration: none; color: aliceblue"
          align="right">
          <el-button>
              <i class="el-icon-arrow-left" style="margin-right: 10px"></i>返回模型实验</el-button>
        </router-link>
      </template>
      <el-descriptions-item label="开始时间">
          {{ modelRun.info.start_time | formatDate}}
      </el-descriptions-item>
      <el-descriptions-item label="Lifecycle Stage">
          {{ modelRun.info.lifecycle_stage}}
      </el-descriptions-item>
      <el-descriptions-item label="责任人">
          {{ modelRun.info.user_id }}
      </el-descriptions-item>
      <el-descriptions-item label="实验状态">
          {{ modelRun.info.status }}
      </el-descriptions-item>
    </el-descriptions>
    <el-divider></el-divider>

     <el-collapse v-model="activeNames">
            <el-collapse-item title="描述" name="1">
                <div> 
                    <slot :description="description" class="main-a">
                    <span v-show="!editModelRunDescVisible">{{modelRun.tags[0].value}}</span>
                    <el-input v-model="editModelForm.description" type="textarea" 
                              :autosize="{ minRows: 4, maxRows: 6}"
                              class="modelDescStyle"
                              v-show="editModelRunDescVisible">
                    </el-input>
                    <el-button  v-show="!editModelRunDescVisible" 
                               class="inline" 
                               icon="el-icon-edit" 
                               type="text"  
                               @click="handleModelDescEdit('index',{description:'description'})" 
                               style="padding: 0px; margin-right: 10px;">
                    </el-button>
                    <br>
                     <span v-show="editModelRunDescVisible">
                        <el-button type="text"  @click.native="editModelRunDescVisible = false" icon="el-icon-back">取消</el-button>
                        <el-button type="text" @click.native="editSubmit" :loading="editLoading" icon="el-icon-check">保存</el-button>
                    </span>
                   </slot>
                </div>
              </el-collapse-item>
          <el-collapse-item title="参数信息" name="2">
              <!--列表-->
              <el-table :data="modelParams"
                        highlight-current-row
                        v-loading="listLoading"
                        style="width: 40%;">
                  <el-table-column prop="key" label="参数名称" min-width="50%">
                  </el-table-column>
                  <el-table-column  prop="value" label="参数值" min-width="50%">
                  </el-table-column>
              </el-table>
           </el-collapse-item>
          <el-collapse-item title="评估指标" name="3">
              <!--列表-->
              <el-table :data="modelMetrics"
                        highlight-current-row
                        v-loading="listLoading"
                        style="width: 40%;">
                  <el-table-column prop="key" label="指标名称" min-width="50%">
                  </el-table-column>
                  <el-table-column  prop="value" label="指标值" min-width="50%">
                  </el-table-column>
              </el-table>
           </el-collapse-item>
       </el-collapse>
  </div>
</template>

<script>

import { test, getModelRun} from '@/api/api'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'

export default {
    data () {
        return {
            _filters: {
                name: ''
            },
            modelRun:{},
            selsModelRunCompare: [],
            modelParams: [],
            modelMetrics: [],
            editLoading:false,
            editModelRunDescVisible: false,
            activeNames: ['2'],
            sels:[],
            total: 0,
            page: 1,
            listLoading: false,
            editModelForm:{
                description: ''
            }
        }
    },
  created () {
    // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
    // this.form.Tasks = this.Tasks[0].id
  },
    filters: {
      formatDate: function (value) {
        let date = new Date(value);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    },
  methods: {
      // 获取模型版本列表
      _getModelRun () {
          this.listLoading = true
          let self = this
          let params = {
              run_id: this.$route.params.run_id,
              user_key: JSON.parse(sessionStorage.getItem('name')),
          }
          let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
          }
          getModelRun(headers, params).then(_data => {
              let { msg, code, data } = _data
              self.listLoading = false
              if (code === '999999') {
                  self.modelRun = data
                  self.modelParams = data.data.params || []
                  self.modelMetrics = data.data.metrics || []
                  console.log(self.modelRun,"self.modelRun")
              } else {
                  self.$message.error({
                      message: msg,
                      center: true
                  })
              }
          })
      },
      // 显示编辑界面
      handleModelDescEdit: function (index, row) {
          this.editModelRunDescVisible = true
          let desc = this.modelRun.tags[0].value || "无"
          // this.editForm = Object.assign({}, row);
          this.editModelForm = Object.assign({}, { description: desc })
      },
      disabledCheckBox(row, index) {
          if (row.is_dir===false) {
              return true
          } else {
              return false
          }
      },
    editSubmit: function(){
        let self = this
        this.editLoading= true
        let param = JSON.stringify({ 
            run_id: this.$route.params.run_id,
            description: this.editModelForm.description,
            user_key: JSON.parse(sessionStorage.getItem('name'))
        })

      console.log(param, 'dsdds')
      $.ajax({
        type: 'post',
        url: test + '/api/experiments/edit_modelrun_description',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            // console.log(data,"dddddd")
            this.editLoading= false
            this.editModelRunDescVisible = false
            this._getModelRun()
            
        }
      })
        
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },
  },
  mounted () {
      this._getModelRun()
  }
}
</script>
